import { compose } from 'redux';
import createACSStoreEnhancer from './createACSStoreEnhancer';
import createAdapter, { applyEgressMiddleware, applyIngressMiddleware, exportDLJSInterface } from './libs';
import { SealedAdapter, AdapterOptions } from './types/AdapterTypes';
import { IDirectLineActivity } from './types/DirectLineTypes';
import { ILogger } from './log/Logger';

import { ACSAdapterState } from './models/ACSAdapterState';
import { IFileManager } from './types/FileManagerTypes';

/**
 * Create an ACS Adapter.
 * @param token An ACS user access token.
 * @param id An ACS user id.
 * @param threadId The chat thread id.
 * @param environmentUrl The ACS resource endpoint.
 * @param fileManager An instance of IFileManager.
 * @param pollingInterval How often (in milliseconds) the adapter will poll for messages in the event notifications are not enabled.
 * @param displayName User display name.
 * @param logger A logger instance.
 * @param adapterOptions AdapterOptions.
 */
export const createACSAdapter = (
  token: string,
  id: string,
  threadId: string,
  environmentUrl: string,
  fileManager: IFileManager,
  pollingInterval: number,
  displayName?: string,
  logger?: ILogger,
  adapterOptions?: AdapterOptions
): SealedAdapter<IDirectLineActivity, ACSAdapterState> => {
  // Ingress and egress middlewares are optional
  const egressMiddleware = adapterOptions && adapterOptions.egressMiddleware ? adapterOptions.egressMiddleware : [];
  const ingressMiddleware = adapterOptions && adapterOptions.ingressMiddleware ? adapterOptions.ingressMiddleware : [];
  return createAdapter(
    {},
    compose(
      exportDLJSInterface(),
      applyEgressMiddleware(...egressMiddleware),
      applyIngressMiddleware(...ingressMiddleware),
      createACSStoreEnhancer(
        token,
        id,
        threadId,
        environmentUrl,
        fileManager,
        pollingInterval,
        displayName,
        logger,
        adapterOptions
      )
    )
  );
};

export * from './filemanager/OneDriveFileManager';
export * from './log/Logger';
export * from './models/ACSAdapterState';
export * from './models/ACSDirectLineActivity';
export * from './types';
