import { SDK } from '../sdk/SDKInit';

export default class ConnectivityManager {
  // check if ACS is connected
  // this functions is called only after SDKInit is done
  public static async isACSConnected(): Promise<boolean> {
    try {
      // acs is not initialized
      if (!SDK.chatThreadClient || !SDK.chatClient) {
        return false;
      }

      // will throw exception if network is not connected
      await SDK.chatThreadClient.getProperties();
      return true;
    } catch (exception) {
      // TODO: Double check this
      if (exception === 'RestError: Failed to send') {
        return false;
      }
    }
    return false;
  }
}
